<script lang="ts">
    import {createEventDispatcher} from "svelte";
    import {fade} from "svelte/transition";
    import ModalButton from "@Core/Modals/Contained/ModalButton.svelte";
    import {EModalAction} from "@Core/Modals/Contained/EModalAction";

    const dispatch = createEventDispatcher<{
        button: EModalAction
    }>();

    export let title: string;
    export let body: string = "";
    export let showClose: boolean = false;
    export let buttons: {
        primary?: string,
        secondary?: string,
        cancel?: string
    }|undefined = undefined;
</script>

<div class="container">
    <div class="bg" in:fade={{duration: 200}}></div>
    <div class="modal">
        <div class="top-bar"></div>
        <div>
            <div class="header">
                {#if showClose}
                    <button type="button" on:click={() => dispatch("button", EModalAction.Cancel)}></button>
                {/if}
                <div class="title">{title}</div>
            </div>
        </div>
        <div class="content">
            <slot>{@html body}</slot>

            {#if buttons && (buttons?.primary || buttons?.secondary || buttons?.cancel)}
                <div class="buttons">
                    {#if buttons?.primary}
                        <ModalButton type="primary" on:click={() => dispatch("button", EModalAction.OK)}>{buttons.primary}</ModalButton>
                    {/if}
                    {#if buttons?.secondary}
                        <ModalButton type="secondary" on:click={() => dispatch("button", EModalAction.Secondary)}>{buttons.secondary}</ModalButton>
                    {/if}
                    {#if buttons?.cancel}
                        <ModalButton type="cancel" on:click={() => dispatch("button", EModalAction.Cancel)}>{buttons.cancel}</ModalButton>
                    {/if}
                </div>
            {/if}
        </div>
    </div>
</div>


<style>
    .container {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 900;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh
    }

    .modal {
        position: relative;
        min-width: 500px;
        max-width: calc(100vw - 100px);
        margin: auto;
        font-family: "Motiva Sans", Sans-serif;
        font-weight: normal;
        background: radial-gradient(circle at top left, rgba(74, 81, 92, 0.4) 0%, rgba(75, 81, 92, 0) 60%), #25282e;
    }

    .top-bar {
        width: 100%;
        height: 1px;
        background: linear-gradient(to right, #00ccff, #3366ff);
    }

    .header {
        padding: 0;
        text-align: left;
        font-size: 22px;
        font-weight: 700;
        color: #ffffff;
        letter-spacing: unset;
        text-transform: unset;
    }

    button {
        border: none;
        background-image: url(data:image/png;base64,);
        background-color: transparent;
        background-repeat: no-repeat;
        background-position: center right;
        cursor: pointer;
        float: right;
        margin-top: 9px;
        margin-right: 9px;
        height: 16px;
        width: 16px;
        opacity: 0.7;
    }
    button:hover {
        opacity: 1;
    }

    .title {
        padding: 24px 24px 0 24px;
        display: block;
    }

    .content {
        width: 100%;
        overflow: auto;
        word-wrap: break-word;
        padding: 24px 25px 25px 25px;
        font-size: 16px;
        line-height: 1.25;
        color: #acb2b8;
        position: relative;
        max-height: calc(100vh - 100px);
        box-sizing: border-box;
    }

    .buttons {
        text-align: right;
    }

    .bg {
        position: fixed;
        background: #000000;
        opacity: 0.8;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
</style>